<template>
  <form @submit="submit()" @submit.prevent method="POST">
    <h1>Add consumer</h1>
    <input
      type="text"
      name="username"
      v-model="consumer.username"
      placeholder="Enter username"
      required
    />
    <input type="email" name="email" v-model="consumer.email" placeholder="Enter email" required />
    <input
      type="text"
      name="nomecognome"
      v-model="consumer.nomeCognome"
      placeholder="Enter name and surname"
      required
    />
    <input
      type="password"
      name="password"
      v-model="consumer.password"
      placeholder="Enter password"
      required
    />
    <input type="submit" value="CONFIRM" />
  </form>
</template>

<script>
import Axios from "axios";
export default {
  name: "AddConsumer",
  data: function () {
    return {
      consumer: {},
    };
  },
  methods: {
    submit: function () {
      Axios.post(
        `${process.env.VUE_APP_APIROOT}uploaderarea/consumermanagment`,
        this.consumer
      )
        .then(response=> console.log("Il server ha risposto con: "+response.status))
        .catch(error=> console.log(error));
    },
  },
};
</script>

<style scoped>
form {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-around;
  align-items: center;
  height: 90%;
  max-height: 500px;
}

input[type="text"],
input[type="email"],
input[type="password"] {
  border-radius: 4px;
  flex-basis: 8%;
  /* flex-grow: 1; */
  width: 30%;
  flex-shrink: 0;
  border-top: none;
  border-right: none;
  border-left: none;
}

input[type="submit"] {
  border-radius: 10px;
  border: 4px solid red;
  flex-basis: 15%;
  width: 30%;
  font-size: 150%;
  font-weight: bolder;
  background-color: orange;
}

input[type="submit"]:hover {
  cursor: pointer;
}
</style>